@import "https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #eee;
  height: 100vh;
  background-color: #222;
  font-family: "Press Start 2P", sans-serif;
}

form {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

h1 {
  margin-bottom: 2rem;
}

.box {
  width: 25%;
  height: 20rem;
  border: 3px solid #000;
  margin: 2% 2% 2% 2rem;
  padding: 1rem;
}

.btn {
  color: #222;
  cursor: pointer;
  visibility: hidden;
  background-color: #eee;
  border: none;
  margin-top: 1rem;
  padding: 1rem;
  font-family: inherit;
  font-size: 1rem;
}

.btn:hover {
  background-color: #a5a5a5;
}

.choice {
  height: 100px;
  text-align: center;
  margin-top: 10px;
  margin-left: 0;
  padding: 0;
}

.choiceBtn {
  color: #222;
  text-shadow: 2px 2px 5px #fff;
  cursor: pointer;
  visibility: visible;
  border: none;
  padding: 0;
  font-family: inherit;
  font-size: 1rem;
}

.choiceBtn#rock {
  width: 100px;
  height: 100px;
  background: url("rock.555226d6.png") no-repeat;
}

.choiceBtn#scissors {
  width: 100px;
  height: 100px;
  background: url("sci.4f83b312.png") no-repeat;
}

.choiceBtn#bag {
  width: 100px;
  height: 100px;
  background: url("bag.006dbf56.png") no-repeat;
}

.choiceBtn:hover {
  opacity: .6;
  background-color: #a5a5a5;
}

.computerName {
  margin-bottom: 3rem;
}

.container {
  width: 100%;
  border: 3px solid #000;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  display: flex;
}

.hidden {
  display: none;
}

.imgIcon {
  width: 5rem;
}

.overlay {
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(3px);
  backdrop-filter: blur(3px);
  z-index: 5;
  background-color: #0009;
  position: absolute;
  top: 0;
  left: 0;
}

.overlayResult {
  text-align: center;
  position: absolute;
  top: 40%;
  left: 30%;
}

#header {
  width: 94%;
  height: 100px;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 10px;
}

#footer {
  width: 94%;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
}

